import { Injectable } from '@vert/core'
import Axios, { AxiosError, AxiosRequestConfig } from 'axios'
import { IApiResponse, IAsyncResult } from '../../types'

const axios = Axios.create({
	timeout: 5000
})

axios.interceptors.request.use(config => {
	if(config.method.toLowerCase() === 'post') {
		config.data = config.data || {}
	}

	config.params = Object.assign(config.params || {}, {
		device: 'h5',
		platform: 'h5'
	})

	return config
})

@Injectable()
class HttpService {
	async get<T = any> (url: string, config?: AxiosRequestConfig): Promise<IAsyncResult> {
	    let result: T = null
	    let error: AxiosError = null
	    let code: number = 0

	    try {
	      const { data: res } = await axios.get<IApiResponse>(url, config)
	      result = res.data
	      code = res.code
	    } catch (err) {
	      error = err
	    }

	    return {
	      data: result,
	      error,
	      code
	    }
	  }

	  async post<T = any> (url: string, data?: any, config?: AxiosRequestConfig): Promise<IAsyncResult> {
	    let result: T = null
	    let error: AxiosError = null
	    let code: number = 0

	    try {
	      const { data: res } = await axios.post<IApiResponse>(url, data, config)
	      result = res.data
	      code = res.code
	    } catch (err) {
	      error = err
	    }

	    return {
	      data: result,
	      error,
	      code
	    }
	  }
}

export {
	HttpService
}


