import { AppComponent, Component } from '@vert/core';
import { ApkDownloadService } from '../../services/apk-download'


@Component
export default class AppDownload extends AppComponent {
	goItuneStore () {
		window.open('https://www.baidu.com');
	}
	goMsStore () {
		window.open('https://www.baidu.com');
	}

	async downloadApk () {
		if(/MicroMessenger/i.test(navigator.userAgent)) {
			window.location.href = 'https://www.baidu.com';
			return;
		}



		const {data, error} = await this.apkDownloadService.getApkDownloadUrl();
		if(error) {
			// ...
		} else {
			const a = document.createElement('a');
			a.href = data;
			a.click()
		}
	}
	onVideoEnded (event) {
		const video = event.target as HTMLVideoElement
		setTimeout(() => video.play(), 4000)
	}

	constructor (
	    private apkDownloadService: ApkDownloadService
	 ) {
	    super();
	    console.log(this.apkDownloadService)
	}
}



