import { Injectable } from '@vert/core'
// import { Http } from '@vert/services'
import { HttpService } from './http'

@Injectable()
class ApkDownloadService {
	async getApkDownloadUrl () {
		const result= {
			data: null,
			error: null
		}
		const {data, error} = await this.HttpService.get(
			'/static/api/download-list.json'
		)
		result.data = data.url
		result.error = error


		return result
	}

	constructor (private HttpService: HttpService) {}
}
export {
	ApkDownloadService
}


