import Vue from 'vue'
import VueRouter from 'vue-router'

import { App, Injector } from '@vert/core'
// import { Http } from '@vert/services'
import { HttpService } from './services/http'
import { ApkDownloadService } from './services/apk-download'



const Services = [
  ApkDownloadService,
  HttpService
]

const injector = Injector.create(...Services)
Services.forEach((Service: any) => {
  App.addSingleton(Service, injector.get(Service))
})



